html,
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: #323232;
}

:root {
    --black: #323232;
    --yellow: #FDB813;
    --white: #F8F8F8;
    --orange: #f04e23;
    --blue: #0daddd;
}

.creditswrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url('./img/bcg.png');
    background-repeat: no-repeat;
    background-size: 100%;
}

.heartimg {
    margin: 90px 0 25px 0;
    width: 120px;
}

h1.creditstitle {
    font-style: normal;
    font-size: 50px;
    font-weight: 700;
}

.authorswrap {
    margin: 50px auto;
}

a {
    color: var(--white);
    text-decoration: none;
    display: block;
    text-align: center;
    margin: 10px auto;
    text-transform: uppercase;
    font-size: 25px;
}

a:hover {
    color: var(--yellow);
}

.btn.secondary.backbtn {
    margin: 20px auto;
}